import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const Some = () => {
  return (
    <p>Hellossssssss</p>
  )
};
const Greeting = (props) => {
  return (
    <p>Hello, {props.name}</p>
  )
};

class App extends React.Component {
  render() {
    return (
      <div>
        <Some />
        <Greeting name="Alyx" />
        <Some />
      </div>
    )
  }
}

ReactDOM.render(<App />,  document.querySelector('.page'));